// pages/accounts.js
import { useEffect, useMemo, useState } from "react";
import Layout from "../components/Layout";
import api from "../utils/api";

function formatCurrency(v) {
  return `${Number(v || 0).toLocaleString("ar-SA", {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  })} Ø±.Ø³`;
}

export default function AccountsPage({ user }) {
  const [transactions, setTransactions] = useState([]);
  const [summary, setSummary] = useState({
    totalIncome: 0,
    totalExpense: 0,
    netTotal: 0,
  });

  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState("");
  const [typeFilter, setTypeFilter] = useState("all");

  // ğŸ”— ØªØ­Ù…ÙŠÙ„ Ø§Ù„Ø¨ÙŠØ§Ù†Ø§Øª Ù…Ù† Ø§Ù„Ø¨Ø§Ùƒ
  useEffect(() => {
    loadTransactions();
  }, []);

  const loadTransactions = async () => {
    try {
      setLoading(true);
      const res = await api.get("/transactions");

      // âœ… Ø§Ù„ØªØ¹Ø¯ÙŠÙ„ Ø§Ù„Ù…Ù‡Ù… Ù‡Ù†Ø§
      setTransactions(res.data.transactions || []);
      setSummary(
        res.data.summary || {
          totalIncome: 0,
          totalExpense: 0,
          netTotal: 0,
        }
      );
    } catch (err) {
      console.error("loadTransactions error:", err);
    } finally {
      setLoading(false);
    }
  };

  // ğŸ” ÙÙ„ØªØ±Ø© Ø§Ù„Ø¹Ù…Ù„ÙŠØ§Øª
  const filteredTransactions = useMemo(() => {
    const q = search.trim().toLowerCase();

    return transactions.filter((t) => {
      const passSearch =
        !q ||
        t.refCode?.toLowerCase().includes(q) ||
        t.userName?.toLowerCase().includes(q);

      const passType =
        typeFilter === "all" || t.type === typeFilter;

      return passSearch && passType;
    });
  }, [transactions, search, typeFilter]);

  return (
    <Layout user={user} title="Ø§Ù„Ø­Ø³Ø§Ø¨Ø§Øª">
      <div dir="rtl" className="space-y-6">
        {/* Ø±Ø£Ø³ Ø§Ù„ØµÙØ­Ø© */}
        <div>
          <h1 className="text-2xl font-bold text-slate-800">ğŸ“Š Ø§Ù„Ø­Ø³Ø§Ø¨Ø§Øª</h1>
          <p className="text-sm text-slate-500">
            Ù…ØªØ§Ø¨Ø¹Ø© Ø§Ù„Ø¥ÙŠØ±Ø§Ø¯Ø§Øª ÙˆØ§Ù„Ù…ØµØ±ÙˆÙØ§Øª ÙˆØ­Ø±ÙƒØ© Ø§Ù„Ø­Ø³Ø§Ø¨Ø§Øª.
          </p>
        </div>

        {/* ÙƒØ±ÙˆØª Ø§Ù„Ù…Ù„Ø®Øµ */}
        <div className="grid grid-cols-1 gap-4 md:grid-cols-3">
          <SummaryCard
            label="Ø¥Ø¬Ù…Ø§Ù„ÙŠ Ø§Ù„Ø¥ÙŠØ±Ø§Ø¯Ø§Øª"
            value={formatCurrency(summary.totalIncome)}
            color="bg-emerald-50 text-emerald-700"
          />
          <SummaryCard
            label="Ø¥Ø¬Ù…Ø§Ù„ÙŠ Ø§Ù„Ù…ØµØ±ÙˆÙØ§Øª"
            value={formatCurrency(summary.totalExpense)}
            color="bg-red-50 text-red-700"
          />
          <SummaryCard
            label="Ø§Ù„ØµØ§ÙÙŠ"
            value={formatCurrency(summary.netTotal)}
            color="bg-sky-50 text-sky-700"
          />
        </div>

        {/* Ø§Ù„Ø¨Ø­Ø« ÙˆØ§Ù„ÙÙ„Ø§ØªØ± */}
        <div className="p-4 bg-white border rounded-xl">
          <div className="flex flex-wrap gap-3">
            <input
              type="text"
              placeholder="Ø¨Ø­Ø« Ø¨Ø§Ù„Ù…Ø±Ø¬Ø¹ Ø£Ùˆ Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù…â€¦"
              className="flex-1 p-2 text-sm border rounded-lg"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />

            <select
              className="p-2 text-sm border rounded-lg"
              value={typeFilter}
              onChange={(e) => setTypeFilter(e.target.value)}
            >
              <option value="all">ÙƒÙ„ Ø§Ù„Ø¹Ù…Ù„ÙŠØ§Øª</option>
              <option value="income">Ø¥ÙŠØ±Ø§Ø¯</option>
              <option value="expense">Ù…ØµØ±ÙˆÙ</option>
            </select>
          </div>
        </div>

        {/* Ø¬Ø¯ÙˆÙ„ Ø§Ù„Ø¹Ù…Ù„ÙŠØ§Øª */}
        <div className="overflow-x-auto bg-white border rounded-xl">
          {loading ? (
            <div className="p-6 text-sm text-center text-slate-500">
              Ø¬Ø§Ø±ÙŠ Ø§Ù„ØªØ­Ù…ÙŠÙ„â€¦
            </div>
          ) : (
            <table className="w-full text-sm text-right min-w-[900px]">
              <thead className="bg-slate-50 text-slate-500">
                <tr>
                  <th className="p-3">#</th>
                  <th className="p-3">Ø§Ù„Ù…Ø±Ø¬Ø¹</th>
                  <th className="p-3">Ø§Ù„Ù†ÙˆØ¹</th>
                  <th className="p-3">Ø§Ù„Ù…Ø¨Ù„Øº</th>
                  <th className="p-3">Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù…</th>
                  <th className="p-3">Ø§Ù„ØªØ§Ø±ÙŠØ®</th>
                </tr>
              </thead>
              <tbody>
                {filteredTransactions.map((t) => (
                  <tr
                    key={t.id}
                    className="border-t even:bg-slate-50/50"
                  >
                    <td className="p-3">{t.id}</td>
                    <td className="p-3">{t.refCode || "-"}</td>
                    <td className="p-3">
                      <span
                        className={`px-2 py-0.5 text-xs rounded-full ${
                          t.type === "income"
                            ? "bg-emerald-50 text-emerald-700"
                            : "bg-red-50 text-red-700"
                        }`}
                      >
                        {t.type === "income" ? "Ø¥ÙŠØ±Ø§Ø¯" : "Ù…ØµØ±ÙˆÙ"}
                      </span>
                    </td>
                    <td className="p-3 font-semibold">
                      {formatCurrency(t.amount)}
                    </td>
                    <td className="p-3">{t.userName || "-"}</td>
                    <td className="p-3">
                      {t.date
                        ? new Date(t.date).toLocaleString("ar-EG")
                        : "-"}
                    </td>
                  </tr>
                ))}

                {!filteredTransactions.length && (
                  <tr>
                    <td
                      colSpan={6}
                      className="p-6 text-center text-slate-400"
                    >
                      Ù„Ø§ ØªÙˆØ¬Ø¯ Ø¹Ù…Ù„ÙŠØ§Øª.
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          )}
        </div>
      </div>
    </Layout>
  );
}

// ğŸ”¹ ÙƒØ±Øª Ø§Ù„Ù…Ù„Ø®Øµ
function SummaryCard({ label, value, color }) {
  return (
    <div className={`p-4 rounded-xl border ${color}`}>
      <p className="text-xs text-slate-500">{label}</p>
      <p className="text-lg font-bold">{value}</p>
    </div>
  );
}





